div.container {
    width: 90%;
    max-width:1000px;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
    margin-top: 80px;
    margin-bottom: 60px;
    display: block;
    font-family: 'AvenirMedium';
    font-size: 13px;
    color: #111;
    min-height: 80vh;
}

.button{
    display: inline-block;
    background: #333;
    color: #fff;
    padding: 10px 5px;
    margin-bottom: 10px;
    text-decoration: none;
    width: 250px;
}

.red-boarder{
    border-color: red !important;
}

.file-uploader {
    margin-bottom: 40px !important;
}

.file-uploader-label {
    margin-bottom: 10px !important;
}

.form-explanation {
    height: auto !important;
    text-indent: 0;
    padding-left: 5px;
    padding-right: 5px;
    max-width: calc(100% - 12px);
}

.form-explanation-label {
    padding: 5px 0;
}

.form-multiple-choise-wrapper {
    padding: 0 5px;
}

.form-multiple-choise-wrapper__items {
    display: flex;
    flex-wrap: wrap;
    min-width: calc(100%);
}

.form-multiple-choise-wrapper__item {
    display: inline-block;
    margin-bottom: 5px;
}
