.calendar{
    text-align: center;
    margin-left: auto;
    margin-right: auto;
    font-family: 'AvenirMedium';
    font-size: 15px;
    width: 90%;
    
    margin-top: 80px;
}

.cal, .charts{
    width: 100%;
    overflow: hidden;
}

.cal{
    max-width: 450px;
    margin-left: auto;
    margin-right: auto;
}

.cal_arrow{
    display: inline-block;
    width: 8px;
    vertical-align: middle;
    cursor: pointer;
}

.cal_arrow:hover{
    opacity: .3;
}

.c_dates{
    display: inline-block;
    vertical-align: middle;
    margin-left: 20px;
    margin-right: 20px;
}

.cal_arrows{
    text-align: center;
    margin-left: auto;
    width: 100%;
    margin-right: auto;
    margin-bottom: 65px;
    font-family: 'AvenirMedium';
    font-size: 15px;
}

.cal_line{
    width: 100%;
    max-width: 435px;
    margin-left: auto;
    margin-right: auto;
    margin-top: 30px;
    margin-bottom: 60px;
    height: 1px;
    background: #666;
}

.cal_headers1{
    display: flex;
    font-size: 15px;
    flex-wrap: wrap;
    height: 40px;
    justify-content: space-between;
    position: relative;
}

.cal_headers{
    display: flex;
    font-size: 15px;
    flex-wrap: wrap;
    height: 60px;
    justify-content: space-between;
    position: relative;
}

.cal_headers_narrow{
    display: flex;
    font-size: 15px;
    flex-wrap: wrap;
    height: 35px;
    justify-content: space-between;
    position: relative;
}

.cal_blocks{
    width: 19%;
    color: #808080;
    font-size: 15px;
}

.cal_fills{
    width: 85%;
    height: 22px;
    position: relative;
    margin-left: auto;
    margin-right: auto;
    border: 1px solid #888;
        cursor: pointer;

}

.c_actives{
    width: 85% !important;
    height: 12px !important;
    opacity: 1 !important;
}

.rights_cal{
    transform: rotate(180deg);

}

.c_green{
    position: absolute;
    left: 50%;
    top: 50%;
    width: 70%;
    opacity: 0;
    transform: translate(-50%, -50%);
    background: #44b07b;
    height: 10px;
        -webkit-transition:all .2s ease-in-out;
    -moz-transition:all .2s ease-in-out;  
    -o-transition:all .2s ease-in-out;
}

.cal_times{
    position: absolute;
    margin-left: -150px;
    margin-top: 45px;
    text-align: right;
    width: 130px;
    font-size: 13px;
    
}

.c_time{
    margin-bottom:42px;
}

.c_time_narrow{
    margin-bottom:17px;
}

.cal_dates{
    width:20%;
    text-align: center;
}


.cal__title {
    position: absolute; top: 0px; left: 0px; z-index: 100; opacity: 1; display: block; visibility: visible;
}

.cal__time {
    margin-bottom: 20px; width: 100%; text-align: left; margin-left: 2px; margin-top: 10px; font-size: 13px
}

.cal__slide {
    position: absolute; top: 0px; left: 0px; z-index: 99; visibility: hidden;
}

.cal__sentinel {
    position: static; top: 0px; left: 0px; z-index: 100; opacity: 1; display: block; visibility: hidden;
}

.title-description {
    margin-top: 5px !important;
    margin-left: 0;
    font-style: italic;
}

.b_texts .alert {
    text-decoration: underline;
} 

.b_texts .description {
    padding-bottom: 10px;
} 

.property-payment {
    font-size: 10pt; 
    font-family: Verdana, arial, Helvetica, sans-serif; 
    width: 100%; 
    border-collapse: collapse; 
    border: 1px solid black
}

.narrow-margin-bottom {
    margin-bottom: 5px;
}


.map_header{
    top: 0;
    padding-top: 110px;
    width: 100%;
    padding-bottom: 40px;
    text-align: center;
    font-family: 'BigCaslon-Medium';
    font-size: 24px;
    z-index: 160;
    color: #4D4E4E;
    position: fixed;
    background: white;
}

.map_holder{
    position: relative;
    width: 100%;
    z-index: 3;
}

.map_btn{
    position: fixed;
    left: 20px;
    top: 215px;
    width: 95px;
    text-align: center;
    z-index: 100000;
    padding-top: 3px;
    padding-bottom: 3px;
    color: white;
    cursor: pointer;
    background: #44b07b;
    border: 2px solid #44b07b;
    font-family: "Gilroy-SemiBold";
    font-size: 13px;
    
}

.map_btn:hover{
    background: #111;
    border: 2px solid #111;
}

.blue_text {
   color: blue;
}

.c_text {
    margin-top: 25px;
    text-align: left;
}

.footer_alert {
    display: flex;
    justify-content: flex-end;
}
.footer_alert .item{
    flex: 0 1 30%;
    text-align: left;
}
@media screen and (max-width:1025px) {
    .footer_alert {
        justify-content: flex-start;
    }
    .footer_alert .item{
        flex: 1 1 100%;
    }
}


/* For dashboard */
.offer_action_btn {
    padding-top: 15px;
    height: 35px;
    width: 130px;
    cursor: pointer;
    background-color: #44b07b;
    color: white;
}

.offer_block {
    border-bottom: 1px solid black;
}

.offer_container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding: 15px 0px;
}

.offer_no,
.incorrect_parameter {
    font-family: 'BigCaslon-Medium';
    font-size: 24px;
    line-height: 30px;
}

.offer_item {
    padding: 0px 15px;	
    /* flex-shrink: 2; */
    font-size: 0.95rem;
}

.offer_item_action {
    text-align: left;
    width: 100%;
    margin-top: 5px;
    margin-bottom: 5px;
}

.green_icon {
    color: #44b07b;
}

.gray_icon {
    color: gray;
}

.red_icon {
    color: red;
}

.offer_item_link {
    margin-left: 10px;
    color: #44b07b;
}

.offer_item_link_disabled {
    color: gray;
}

.offer_item_link:hover {
    text-decoration: underline !important;
}


.offer_item .offer_title {
    font-weight: 600;
    padding-top: 15px;
    padding-bottom: 15px;
}

.offer_item .offer_text {
    font-weight: 300;
}

.hiw_block_front {
    width: 49.5%;
}

.hiw_section_front {
    padding-top: 15px;
    min-height: 80vh;
}

.hiw_blocks_front {
    padding-top: 50px !important;
    margin-top: 25px !important;
}

@media screen and (max-width:500px) {
    .offer_action_btn {
        width: 100%;
        text-align: center;
    }

    .hiw_block_front {
        width: 100%;
    }

    .offer_item {
        text-align: left !important;
        flex-basis: 500px;
        padding-top: 5px;
        padding-bottom: 15px;
        padding-left: 0px;
        padding-right: 0px;
    }

    .offer_block {
        margin-bottom: 15px;
    }

    .offer_block:last-child {
        border-bottom: none;
    }

    .hiw_holder {
        padding-bottom: 15px;
    }
}


.property_data_row {
    display: flex;
    flex-wrap: wrap;
}

.property_data_title {
    flex-basis: 130px;
    font-weight: bold;
}

.property_data_item {
    flex-basis: 450px;
}

.list_price_container {
    min-height: 0vh !important;
}

@media screen and (min-width:768px) {
    .list_price_container {
        width: 540px !important;
    }
}

.list_price_slider_tooltips {
    display: flex;
    justify-content: space-between;
    margin-top: 15px;
}

.list_price_slider_tooltips_item {
    padding: 5px 10px;
    font-weight: 800;
}

.list_price_selected_price {
    font-weight: 300;
    background-color: #44b07b;
    color: white;
}

.listing_confirm_container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-bottom: 40px;
    margin-top: 0px;
    width: 100%;
    box-sizing: border-box;
    border: 1px solid #333;
    padding-top: 5px;
    padding-bottom: 0px;
}

.listing_confirm_left_panel {
    margin: 20px;
    flex-basis: 45%;
}

.listing_confirm_right_panel {
    margin: 20px;
    flex-basis: 45%;
}

.listing_confirm_row {
    display: flex;
    flex-wrap: wrap;
    font-size: 14px;
}

.listing_confirm_title {
    flex-basis: 40%;
    font-weight: bold;
}

.listing_confirm_item {
    flex-basis: 60%;
}

.closing_costs_container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-bottom: 0px;
    margin-top: 0px;
    width: 100%;
    padding-top: 5px;
    padding-bottom: 15px;
}

.closing_costs_left_panel {
    margin: 10px;
    flex-basis: 45%;
}

.closing_costs_right_panel {
    margin: 10px;
    flex-basis: 45%;
}

.closing_costs_row {
    display: flex;
    flex-wrap: wrap;
    font-size: 14px;
    padding-top: 5px;
    padding-bottom: 5px;
}

.closing_costs_title {
    flex-basis: 65%;
    color: #808080;
    font-family: 'AvenirLTCom-Roman';
}

.closing_costs_item {
    flex-basis: 30%;
    font-family: 'AvenirMedium';
    color: #333;
}

.closing_costs_title2{
    margin-bottom: 15px;
    margin-top: 15px;
}


.closing_costs_box{
    width: 100%;
    border: 2px solid black;
    padding: 7px;
    margin: 0px; 
}


@media screen and (max-width:1025px) {
    .listing_confirm_left_panel {
        flex-basis: 100%;
    }
    
    .listing_confirm_right_panel {
        flex-basis: 100%;
    }

    .closing_costs_left_panel {
        flex-basis: 100%;
    }
    
    .closing_costs_right_panel {
        flex-basis: 100%;
    }
}

.table_container {
    display: flex;
    flex-wrap: wrap;
}

.table_head {
    color: black;
}

.table_cell {
    padding: 5px 5px;
    flex-basis: 190px;
}

.table_cell_row_number {
    color: #44b07b;
    font-size: 0.95rem;
    font-weight: 600;
    padding-top: 25px;
}

.table_row_separator {
    flex-basis: 100%;
    margin-top: 10px;
    margin-bottom: 10px;
}

.title_component-container {
    display: flex;
    flex-wrap: wrap;
    max-width: 100%;
    padding-bottom: 10px;
}

.title_component-item {
    flex-basis: 25%;
    max-width: 240px;
    padding: 6px 0;
}

@media screen and (max-width:768px) {
    .table_cell {
        padding: 5px 0px 5px 0px;
        flex-basis: 100% !important;
    }

    .table_cell_row_number {
        padding-top: 25px;
        padding-bottom: 25px;
    }
    
    .title_component-item {
        flex-basis: 50%;
    }
}
